.button {
  display: inline-block;
  padding: 14px 26px;
  font-family: 'Roboto Condensed', Helvetica, sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  cursor: pointer; }
  .button:hover, .button:focus {
    opacity: 0.7; }
  .button:active {
    opacity: 0.3; }

a {
  color: inherit;
  text-decoration: none; }
  a:hover, a:focus {
    text-decoration: underline; }
  a:active {
    opacity: 0.7; }

.page {
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-family: 'PT Mono', Courier, monospace;
  color: #666666;
  background-color: #FFFFFF; }

.page h1 {
  font-size: 80px;
  font-weight: 700;
  font-family: 'Roboto Condensed', Helvetica, sans-serif;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin-left: -4px; }

.page h2 {
  font-size: 36px;
  line-height: 51px; }

.page h3 {
  font-size: 24px;
  line-height: 34px; }

@media (max-width: 1023px) {
  .page h1 {
    font-size: 60px;
    line-height: 70px; } }

@media (max-width: 767px) {
