.contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-width: 693px;
  padding-bottom: 6px; }

.contacts__link {
  position: relative;
  margin-left: 35px;
  padding-left: 38px; }

.contacts__link svg {
  position: absolute;
  top: 2px;
  left: 0;
  fill: #00B2FF; }

.contacts__link--tel svg {
  left: 4px; }

@media (max-width: 1023px) {
  .contacts {
    min-width: 108px; }
  .contacts__button {
    display: none; }
  .contacts__link {
    display: inline-block;
    height: 24px;
    margin-right: 0;
    margin-left: 25px;
    padding-left: 24px; }
  .contacts__link span {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden; }
  .contacts__link:hover svg,
  .contacts__link:focus svg {
    fill: #FFFFFF; }
  .contacts__link--mail svg {
    top: 1px;
    width: 26px;
    height: 19px; }
  .contacts__link--tel svg {
    left: 0;
    top: -2px;
    width: 24px;
    height: 24px; } }

@media (max-width: 767px) {
  .contacts {
    min-width: 88px; }
  .contacts__link {
    margin-left: 15px; } }
